<template lang="pug">
.container-fluid
  .img(:style="{ backgroundImage: 'url(' + banner + ')' }")
    .title.text-center
      h2.i_h2_title 創新之光 Enterprise Partner
      p.subtitle.i_body_l 打造專屬您產業模式需求的網頁包套方案
      svg-icon(iconClass="arrow_bottom", className="arrow_bottom")
  .container.i_mtb_10
    nav.d-flex.i_pl_4
      .sheet(
        @click="sheetSelected(index, sheet.pageName)",
        v-for="(sheet, index) in sheets",
        :class="{ selected: selectedIndex == index }"
      ) {{ sheet.title }}

  transition(name="page", mode="out-in")
    router-view(:key="$route.path")
</template>

<script>
import banner from "@/assets/img/solution/banner2.png";

export default {
  name: "Top",
  data() {
    return {
      banner,
      selectedIndex: 0,
      sheets: [
        { title: "產品概念", pageName: "SolutionConcept" },
        { title: "方案介紹", pageName: "SolutionDescription" },
        { title: "作品展示", pageName: "" },
      ],
    };
  },
  mounted() {
    this.getCurrentPagePath();
  },
  methods: {
    sheetSelected(index, pageName) {
      this.selectedIndex = index;
      this.toPage(pageName);
    },
    toPage(pageName) {
      this.$router.push({ name: pageName });
    },
    getCurrentPagePath() {
      if (this.$route.path.indexOf("concept") != -1) {
        this.selectedIndex = 0;
      } else if (this.$route.path.indexOf("description") != -1) {
        this.selectedIndex = 1;
      }
    },
  },
};
</script>

<style lang="scss" scoped>
@import "@/assets/style/works/solution/top";
</style>
